<script>
import { Header as AppHeader, SidebarToggler, Sidebar as AppSidebar, SidebarFooter, SidebarForm, SidebarHeader, SidebarMinimizer, SidebarNav, Aside as AppAside, AsideToggler, Footer as TheFooter, Breadcrumb } from '@coreui/vue'
import DefaultAside from './DefaultAside'
import gql from 'graphql-tag'
import DefaultHeaderDropdownAccnt from './DefaultHeaderDropdownAccnt'

export default {
  name: 'DefaultContainer',
  components: {
    AsideToggler,
    AppHeader,
    AppSidebar,
    AppAside,
    TheFooter,
    Breadcrumb,
    DefaultAside,
    DefaultHeaderDropdownAccnt,
    SidebarForm,
    SidebarFooter,
    SidebarToggler,
    SidebarHeader,
    SidebarNav,
    SidebarMinimizer
  },
  data () {
    return {
      nav: []
    }
  },
  created(){
    const that = this;
    this.$apollo.query({
      query: gql`
      {
        data:__schema{
          types{
            name
            kind
            fields{
              name
            }
          }
        }
      }`
    }).then(res => {
      const data = res.data.data.types
      const nav = [];
      console.log(data);
      data.map(item=>{
        if(item.name.indexOf('__') === -1 
        && item.kind !== 'SCALAR'
        &&  item.name !== 'Query'
        &&  item.name !== 'Mutation'
        &&  item.name !== 'Payload'
        &&  item.name !== 'Node'
        &&  item.name !== 'Pagination'
        
        ){
          nav.push({
            name: item.name,
            url: '/'+item.name
          })
        }
      })
      console.log(nav);
      that.nav = nav;
    });
    this.nav =  [{
      name: 'Post',
      url: '/posts',
    }];
  },
  computed: {
    name () {
      return this.$route.name
    },
    list () {
      return this.$route.matched.filter((route) => route.name || route.meta.label )
    }
  }
}
</script>
